@notice-prefix-cls: ~"@{css-prefix}notice";
@icon-prefix-cls: ~"@{css-prefix}icon";

@notice-width: 335px;
@notice-padding: 16px;
@notice-margin-bottom: 10px;

.@{notice-prefix-cls} {
    width: @notice-width;
    margin-right: 24px;
    position: fixed;
    z-index: @zindex-notification;

    &-notice {
        margin-bottom: @notice-margin-bottom;
        padding: @notice-padding;
        border-radius: @border-radius-small;
        box-shadow: @shadow-base;
        background: @primary-color;
        line-height: 1;
        position: relative;
        overflow: hidden;
        padding: 40px 9px 16px 6px;
        &-content {
            background-color: white;
        }
        &-close {
            position: absolute;
            right: 16px;
            top: 15px;
            color: #fff !important;
            outline: none;

            i{
                .close-base(-3px);

                .ivu-icon-information-circled {
                    position: relative;
                    top: 18px;
                }
            }

            .ivu-icon-ios-close-empty:before {
                content: "\F404";
                color: white;
            }

        }

        &-with-desc{
            .@{notice-prefix-cls}-notice-close{
                top: 11px;
            }
        }
    }

    &-title {
        font-size: @font-size-base;
        color: @header-color;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        position: absolute;
        top: 14px;
        left: 15px;
        padding-right: 16px;
    }
    &-with-desc &-title{
        margin-bottom: 8px;
    }
    &-with-desc&-with-icon &-title{
        margin-left: 0px;
    }

    &-desc {
        font-size: 12px;
        background-color: #f4f6f9;
        color: @legend-color;
        text-align: justify;
        line-height: 1.5;
        div {
            padding: 18px 10px;
        }
    }
    &-with-desc&-with-icon &-desc{
        padding-left: 51px;
    }

    &-with-icon &-title{
        margin-left: 26px;
    }

    &-icon {
        position: absolute;
        left: 20px;
        margin-top: -1px;
        font-size: 16px;
        padding-top: 18px;
        &-success {
            color: @success-color;
        }
        &-info {
            color: @primary-color;
        }
        &-warning {
            color: @warning-color;
        }
        &-error {
            color: @error-color;
        }
    }
    &-with-desc &-icon{
        font-size: 36px;
    }

    &-custom-content{
        &:after{
            content: "";
            display: block;
            width: 4px;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
        }
        &-noDesc {
            background-color: white;
        }
    }
    &-with-normal{
        &:after{
            background: @primary-color;
        }
    }
   /* &-with-info{
        &:after{
            background: @primary-color;
        }
    }
    &-with-success{
        &:after{
            background: @success-color;
        }
    }
    &-with-warning{
        &:after{
            background: @warning-color;
        }
    }
    &-with-error{
        &:after{
            background: @error-color;
        }
    }*/
}


