@transfer-prefix-cls: ~"@{css-prefix}transfer";
@transfer-item-prefix-cls: ~"@{css-prefix}transfer-list-content-item";

.@{transfer-prefix-cls} {
    position: relative;
    line-height: @line-height-base;

    &-list{
        display: inline-block;
        /*width: 180px;*/
        width: 220px;
        height: 210px;
        font-size: @font-size-small;
        vertical-align: middle;
        position: relative;
        padding-top: 35px;

        &-with-footer{
            padding-bottom: 35px;
        }

        &-header {
            padding: 8px 16px;
            background: @head-bg;
            color: @text-color;
            border: 1px solid @border-color-base;
            border-bottom: 1px solid @border-color-split;
           /* border-radius: @border-radius-base @border-radius-base 0 0;*/
            overflow: hidden;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;

            & > span{
                padding-left: 4px;
            }

            &-count {
                margin: 0 !important;
                float: right;
            }
        }

        &-body{
            height: 100%;
            border: 1px solid @border-color-base;
            border-top: none;
           /* border-radius: 0 0 @border-radius-base @border-radius-base;*/
            position: relative;
            overflow: hidden;
            &-with-search{
                padding-top: 34px;
            }
            &-with-footer{
                border-radius: 0;
            }
        }

        &-wrap {
            overflow: hidden;
            height: 100%;
        }

        &-content{
            height: 100%;
            padding: 4px 0;
            overflow: auto;
            margin-right: 5px;
            &-item{
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;

                & > span{
                    padding-left: 4px;
                }
            }

            &-not-found{
                display: none;
                text-align: center;
                color: @btn-disable-color;
            }
            li&-not-found:only-child{
                display: block;
            }
        }
        &-body-with-search &-content{
            padding: 6px 0 0;
        }

        &-body-search-wrapper{
            padding: 8px 8px 0;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
        }

        &-search{
            position: relative;
        }

        &-footer{
            border: 1px solid @border-color-base;
            border-top: none;
           /* border-radius: 0 0 @border-radius-base @border-radius-base;*/
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;

            .clearfix();
        }
    }
    &-operation {
        display: inline-block;
        overflow: hidden;
        margin: 0 16px;
        vertical-align: middle;

        .@{btn-prefix-cls} {
            display: block;
            min-width: @btn-circle-size-small;

            &:first-child {
                margin-bottom: 12px;
            }
        }
    }
    /*穿梭框样式*/
    .ut-transfer-tree-indent,
    .ut-transfer-tree-swc{
        width: 18px;
        height: 18px;
        line-height: 18px;
        vertical-align: middle;
        display: inline-block;
    }
    .ut-transfer-tree-indent {
        background-position: -1000px 0;
    }
    .ut-transfer-tree-swc {
        background-position: -120px 0;
    }
    .ut-expanded .ut-transfer-tree-swc {
        transform: rotate(90deg);
        background-position: -140px 0;
    }
    .ut-transfer-tree-leaf .ut-transfer-tree-swc {
        display: none;
        background-position: -1000px 0;
    }
    .ut-hidden {
        display: none!important;
    }
    .remove-hidden{
        display: none!important;
    }
    .rowOperate{
    }
}
.select-item(@transfer-prefix-cls, @transfer-item-prefix-cls);
/*滚动条样式*/
::-webkit-scrollbar {
    height: 8px;
    width: 8px;
    background: rgba(255, 255, 255, 0.1);
}
::-webkit-scrollbar:hover {
    background: rgba(255, 255, 255, 0.2);
}
::-webkit-scrollbar-thumb {
    background: rgba(205, 211, 222, 1);
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
    -webkit-box-shadow: inset 1px 1px 1px rgba(205, 211, 222, 0.9);
    /* Webkit browsers */
    -moz-box-shadow: inset 1px 1px 1px rgba(205, 211, 222, 0.9);
    /* Firefox */
    -ms-box-shadow: inset 1px 1px 1px rgba(205, 211, 222, 0.9);
    /* IE9 */
    -o-box-shadow: inset 1px 1px 1px rgba(205, 211, 222, 0.9);
    /* Opera(Old) */
    box-shadow: inset 1px 1px 1px rgba(205, 211, 222, 0.9);
    /* IE9+, News */
    background-color: rgba(205, 211, 222, 0.9);
}


