@tree-prefix-cls: ~"@{css-prefix}tree";

.@{tree-prefix-cls} {
    ul{
        list-style: none;
        margin: 0;
        padding: 0;
        font-size: @font-size-small;
        li{
            list-style: none;
            margin: 0px;
            padding: 0px;
            white-space: nowrap;
            outline: none;
        }
    }
    li{
        ul{
            margin: 0;
            padding: 0 0 0 18px;
        }
        img{
            vertical-align: middle;
        }
    }
    &-listItem{
        cursor: pointer;
        line-height: 35px;
        &:hover {
            background-color: tint(@primary-color, 90%);
        }
        // &-selected, &-selected:hover{
        //     background-color: tint(@primary-color, 80%);
        // }
    }
    &-title {
        display: inline-block;
        margin: 0;
        padding: 0 4px;
        border-radius: @btn-border-radius-small;
        cursor: pointer;
        vertical-align: top;
        color: @text-color;
        transition: all @transition-time @ease-in-out;
        // &:hover {
        //     background-color: tint(@primary-color, 90%);
        // }
        &-selected, &-selected:hover{
           color: #3a68ad;
           font-weight: bold;
        }
        &-target{
           color: red;
        }
    }
    &-arrow{
        cursor: pointer;
        vertical-align: middle;
        i{
            transition: all @transition-time @ease-in-out;
        }
        &-open{
            i {
                transform: rotate(90deg);
            }
        }
        &-hidden{
            cursor: auto;
            i{
                /*display: none;*/
                visibility: hidden;
            }
        }
        &-disabled{
            cursor: @cursor-disabled;
        }
        &-loading:after{
            content: '';
            margin-left: 4px;
            height: 10px;
            width: 10px;
            display: inline-block;
            font-size: 0;
            vertical-align: middle;
            border: 1px solid #0087ce;
            border-color: #0087ce transparent #0087ce #0087ce;
            border-radius: 8px;
            animation: nodeloading .6s linear infinite;
        }
    }
}

@keyframes nodeloading {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}


