@page-prefix-cls: ~"@{css-prefix}page";

.@{page-prefix-cls} {
    &:after {
        content: '';
        display: block;
        height: 0;
        clear: both;
        overflow: hidden;
        visibility: hidden;
    }

    &-item {
        float: left;
        min-width: @btn-circle-size;
        height: @btn-circle-size;
        line-height: @btn-circle-size - 2px;
        //注释掉
        //margin-right: 4px;
        text-align: center;
        list-style: none;
        background-color: #fff;
        user-select: none;
        cursor: pointer;
        font-family: Arial;
        //border: 1px solid @border-color-base;
        //border-top:1px solid @border-color-base;;
        //border-bottom: 1px solid @border-color-base;;
        border: 1px solid @border-color-split;
        border-left:0px;
        //中间每一格 注释掉
        //border-radius: @btn-border-radius;
        transition: all @transition-time @ease-in-out;

        a {
            margin: 0 6px;
            text-decoration: none;
            color: @text-color;
            //transition: none;
        }

        &:hover {
            //划入改动
            background-color: #e8f3fc;

            //border-color: @primary-color;
           // a {
                //color: @primary-color;
                //color: #3e94cf;
           // }
        }

        &-active {
            //选中背景色替换
            //background-color: @primary-color;
            //border-color: @primary-color;
            //background-color: #f2f8fc;
            //border-color: #03a9f4;
            a, &:hover a {
                color: #3a68ad;
                font-weight:bold;
            }
        }
    }

    &-item-jump-prev, &-item-jump-next {
        &:after {
            content: "•••";
            display: block;
            letter-spacing: 1px;
            color: #ccc;
            text-align: center;
        }

        i{
            display: none;
        }

        &:hover {
            &:after{
                display: none;
            }
            i{
                display: inline;

            }
            background-color: #e8f3fc;
        }
    }

    &-item-jump-prev:hover {
        i:after {
            content: "\F3D2";
        }
    }

    &-item-jump-next:hover {
        i:after {
            content: "\F3D3";
        }
    }

    &-begin{
        //上一页 注释掉
        //margin-right: 8px;
        border-bottom-left-radius: 2px;
        border-top-left-radius: 2px;
    }

    &-item-jump-prev,
    &-item-jump-next{
        //注释掉
        //margin-right: 4px;
    }

    &-end{
        //下一页 注释掉
        //margin-left: 4px;
        border-bottom-right-radius: 2px;
        border-top-right-radius: 2px;
    }

    &-begin,
    &-end,
    &-prev,
    &-next,
    &-item-jump-prev,
    &-item-jump-next {
        display: inline-block;
        float: left;
        min-width: @btn-circle-size;
        height: @btn-circle-size;
        line-height: @btn-circle-size - 2px;
        list-style: none;
        text-align: center;
        cursor: pointer;
        color: #666;
        font-family: Arial;
        border: 1px solid @border-color-split;
        //注释掉
        //border-radius: @btn-border-radius;
        transition: all @transition-time @ease-in-out;
    }

    &-begin,
    &-end,
    &-prev,
    &-next {
        background-color: #fff;

        a {
            color: #666;
            font-size: 14px;
        }

        &:hover {
            //划入改动
            background-color: #f2f8fc;
            //border-color: @primary-color;

            a {
                //color: @primary-color;
                color: #3e94cf;
            }
        }
    }
    &-prev,
    &-next,
    &-end,
    &-item-jump-prev,
    &-item-jump-next{
        border-left:0px;
    }

    &-disabled {
        cursor: @cursor-disabled;
        background-color: #dee2ea!important;
        a {
            color: #ccc!important;;
        }
        &:hover {
            border-color: @border-color-split;
            a {
                color: #ccc;
                cursor: @cursor-disabled;
            }
        }
    }

    &-options {
        float: left;
        margin-left: 15px;
        &-sizer {
            float: left;
            margin-right: 10px;
        }

        &-elevator {
            float: left;
            height: @btn-circle-size;
            line-height: @btn-circle-size;

            input {
                .input;
                border-radius: @btn-border-radius;
                margin: 0 8px;
                width: 50px;
            }
        }
    }

    &-total {
        float: left;
        height: @btn-circle-size;
        line-height: @btn-circle-size;
        margin-right: 10px;
    }

    &-simple &-prev,
    &-simple &-next {
        margin: 0;
        border: 1px solid @border-color-split;
        height: 30px;
        width:31px;
        line-height: 30px;
        font-size: 20px;
    }
    &-simple &-prev,
    &-simple &-next{
        border-radius:2px;
    }

    &-simple &-simple-pager {
        float: left;
        margin-right: 8px;

        input {
            width: 31px;
            height: 30px;
            margin: 0 8px;
            //padding: 5px 8px;
            text-align: center;
            box-sizing: border-box;
            background-color: #fff;
            outline: none;
            border: 1px solid @border-color-split;
            border-radius: @btn-border-radius;
            transition: border-color @transition-time @ease-in-out;

            &:hover {
                border-color: @primary-color;
            }
        }

        span{
            padding: 0 8px 0 2px;
        }
    }
}

.@{page-prefix-cls} {
    &.mini &-total {
        height: @btn-circle-size-small;
        line-height: @btn-circle-size-small;
    }

    &.mini &-item {
        border: 0;
        margin: 0;
        min-width: @btn-circle-size-small;
        height: @btn-circle-size-small;
        line-height: @btn-circle-size-small;
        border-radius: @btn-border-radius-small;
    }

    &.mini &-prev,
    &.mini &-next {
        margin: 0;
        min-width: @btn-circle-size-small;
        height: @btn-circle-size-small;
        line-height: @btn-circle-size-small;
        border: 0;

        a {
            i:after {
                height: @btn-circle-size-small;
                line-height: @btn-circle-size-small;
            }
        }
    }

    &.mini &-item-jump-prev,
    &.mini &-item-jump-next {
        height: @btn-circle-size-small;
        line-height: @btn-circle-size-small;
        border: none;
        margin-right: 0;
    }

    &.mini &-options {
        margin-left: 8px;
        &-elevator {
            height: @btn-circle-size-small;
            line-height: @btn-circle-size-small;

            input {
                .input-small;
                width: 44px;
            }
        }
    }
}



