@login-prefix-cls: ~"@{css-prefix}login";
.@{login-prefix-cls} {

  &-wrap {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
  }
  &-pwd {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 6px;
    color: #abb5bf;
  }
  &-a {
    color: #abb5bf !important;
      &:active {
        color: #3a68ad !important;
      }
      &:hover {
      color: #3a68ad !important;
    }
  }
}
.getPwd {
  color: #4d4d4d;
}




