@alert-prefix-cls: ~"@{css-prefix}alert";
@icon-prefix-cls: ~"@{css-prefix}icon";

.@{alert-prefix-cls}{
    position: relative;
    padding: 8px 48px 8px 16px;
    color: @alert-content-color;
    font-size: @font-size-small;
    line-height: 16px;
    margin-bottom: 10px;
    text-align: center;

    .@{alert-prefix-cls}-close .@{icon-prefix-cls}-ios-close-empty{
        color: @alert-content-color;
    }

    .@{alert-prefix-cls}-content {
        display: inline-block;
        text-align: left;
        margin-left: 5px; 
        max-width: 80%;
        vertical-align: middle;
    }
    &&-with-icon{
        padding: 8px 48px 8px 38px;
    }

    &-icon {
        font-size: @font-size-base;
        // top: 8px;
        // left: 16px;
        // position: absolute;
        vertical-align: middle;
    }

    &-desc {
        font-size: @font-size-small;
        line-height: 21px;
        display: none;
        text-align: justify;
    }

    &-success {
        border: 1px solid @success-color;
        background-color: @success-color;
        .@{alert-prefix-cls}-icon {
            color: @alert-content-color;
        }
    }

    &-info {
        border: 1px solid @primary-color;
        background-color: @primary-color;
        .@{alert-prefix-cls}-icon {
            color: @alert-content-color;
        }
    }

    &-warning {
        border: 1px solid @warning-color;
        background-color: @warning-color;
        .@{alert-prefix-cls}-icon {
            color: @text-color;
        }
        .@{alert-prefix-cls}-close .@{icon-prefix-cls}-ios-close-empty{
            color: @text-color;
        }
    }

    &-error {
        border: 1px solid @error-color;
        background-color: @error-color;
        .@{alert-prefix-cls}-icon {
            color: @alert-content-color;
        }
    }

    &-close {
        .content-close(-3px);
    }

    &-with-desc {
        padding: 16px;
        position: relative;
        margin-bottom: 10px;
        line-height: 1.5;
    }

    &-with-desc&-with-icon{
        padding: 16px 16px 16px 69px;
    }

    &-with-desc &-desc{
        display: block;
    }

    &-with-desc &-message {
        font-size: 14px;
        display: block;
    }

    &-with-desc &-icon {
        top: 50%;
        left: 24px;
        margin-top: -21px;
        font-size: 28px;
    }

    &-with-banner{
        border-radius: 0;
    }
}
.@{alert-prefix-cls}-warning{
    color: @text-color;
}


