@noticeModal-prefix-cls: ~"@{css-prefix}noticeModel";

.@{noticeModal-prefix-cls} {
  height:  296px;
  min-height: 60px;
  width: 464px ;
  min-width: 180px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  z-index: 999;
  &-tipTop {
    float: right;
    position: absolute;
    top: -8px;
    right: 40px;
    width: 10px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #ffffff;
    z-index: 999999999;
    filter: drop-shadow(-1px -4px 2px #cdd3de);
  }
  &-tipBot {
      float: right;
      position: absolute;
      bottom: -8px;
      right: 40px;
      width: 10px;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid #ffffff;
      z-index: 999999999;
      filter: drop-shadow(-1px 5px 2px #cdd3de);
  }
  &-info {
    width: 100%;
    height: 286px;
    box-shadow: 0px 0px 10px 2px #cdd3de;
    border: 1px solid #cdd3de;
  }
  &-content {
    padding: 15px 16px;
  }
}


