@input-prefix-cls: ~"@{css-prefix}input";
.ivu-icon-left .ivu-input-icon {
    left: 0px;
}
.ivu-icon-left .ivu-input-icon-normal + .ivu-input {
    padding-left: 32px;
}
.ivu-icon-error{
    position: absolute;
    right: 0;
    width: 32px;
    height: @input-height-base;
    line-height: @input-height-base;
    font-size: 20px;
    text-align: center;
    color: @input-icon-color;
    z-index: 3;
}
.ivu-form-item-error .ivu-icon-right-error:before{
    content: "\F406";
    color: @error-color;
}
.ivu-form-item-success .ivu-icon-right-error:before{
    display: block;
    content: "\F3ff";
    color: @success-color;
}
.@{input-prefix-cls} {
    .input;
    &-wrapper{
        display: inline-block;
        width: 200px;
        /*width: 100%;*/
        position: relative;
        vertical-align: middle;
    }
    &-icon {
        width: 32px;
        height: @input-height-base;
        line-height: @input-height-base;
        font-size: 20px;
        text-align: center;
        /*color: @primary-color;*/
        position: absolute;
        right: 0;
        z-index: 3;
    }
    &-hide-icon &-icon{
        display: none;
    }
    &-icon-validate{
        display: none;
    }

    &-icon-normal + &{
        padding-right: 32px;
    }
    // #554
    &-hide-icon &-icon-normal + &{
        padding-right: @input-padding-horizontal;
    }

    &-wrapper-large &-icon{
        font-size: 18px;
        height: @input-height-large;
        line-height: @input-height-large;
    }
    &-wrapper-small &-icon{
        width: 24px;
        font-size: 14px;
        height: @input-height-small;
        line-height: @input-height-small;

        //+ .@{input-prefix-cls} {
        //    padding-right: 24px;
        //}
    }
}

.@{input-prefix-cls}-group{
    .input-group(~"@{input-prefix-cls}");
}

.@{form-item-prefix-cls}-error{
    .@{input-prefix-cls}{
        .input-error;
        &-icon{
            color: @error-color;
        }
    }
    .@{input-prefix-cls}-group{
        .input-group-error;
    }
    .@{transfer-prefix-cls} {
        .@{input-prefix-cls} {
            .input;
            &-icon{
                color: @subsidiary-color;
            }
        }
    }
}
.@{form-item-prefix-cls}-validating{
    .@{input-prefix-cls}{
        &-icon-validate{
            display: inline-block;
        }
        &-icon + .@{input-prefix-cls}{
            padding-right: 32px;
        }
    }
}


