.ts-wrap {
  .tree-select {
    position: absolute;
    z-index:999;
    .ts-body {
      width: 160px;
      border: 1px solid #c0c0c0;
      background: #ffffff;
      margin-top: 2px;
      .ivu-tree {
        padding-left: 10px;
        height: 100%;
        overflow: auto;
      }
    }
  }
  .ts-show-clear{
    &:hover .ts-icon{
      font-size: 20px;
      color: #9fa9b2;
      display: block;
      position: absolute;
      top:1px;
      right: 10px;
    }
    &:hover .ivu-input-icon{
      display: none;
    }
  }
  .ts-one-input {
    .node {
      /*width: 100%;*/
      position: absolute;
      top: 0;
      color: #4d4d4d;
      line-height:32px;
      span{
        display: inline-block;
        padding: 0px 5px;
      }
    }
    .ts-icon{
      display: none;
      position: absolute;
      top:1px;
      right: 10px;
    }
  }
  .ivu-tree-arrow {
    display: inline-block;
    /* float: left; */
    cursor: pointer;
    vertical-align: middle;
  }
  .item {
      position: absolute;
      top: 0;
        li{
          float: left;
          span {
          float: left;
          color: #2d8cf0;
          border: 1px solid #2d8cf0;
          background-color: #cce7f6;
          padding: 3px 5px;
          border-radius: 3px;
          margin: 7px;
          min-width: 128px;
        }
      }
  }
  .ts-input .icon{
    float: right;
  }
  .icon {
    /*float: right;*/
  }
  .pl {
    padding-left: 5px;
    margin-top: 3px;
  }
  .pr{
    padding-right: 3px;
  }
  .ivu-treeSelect {
    min-height: 32px;
    border-radius: 3px;
    border: 1px solid #b4c1c9;
    width: 160px;
    box-sizing: border-box;
    vertical-align: middle;
    color: #3a4951;
    font-size: 14px;
    line-height: normal;
    display: flex !important;
    flex-direction: row;
    align-items: center;
    justify-content:space-between;
    .multIcon {
      padding-right: 10px;
      min-height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .ivu-tag {
      margin-left: 2px !important;
      color: @text-color;
      background-color: #eceef0;
    }
    span.ivu-select-placeholder {
      display: block;
      height: 30px;
      line-height: 30px;
      color: #c3cbd6;
      font-size: 12px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding-left: 4px;
      padding-right: 22px;
    }
  }
  .ivu-select-selection {
    border: 0 !important;
      input {
        border:0 !important;
        height: 30px !important;
      }
      input.ivu-input:focus {
        outline: none;
        border: 0;
        box-shadow: 0 0 0 1000px white inset;
      }
  }
}
.ts-body {
  width: 160px;
  border: 1px solid #c0c0c0;
  background: #ffffff;
  margin-top: 2px;
  .ivu-tree {
    padding-left: 10px;
    height: 100%;
    overflow: auto;
  }
}
.ivu-treeSelect-transfer{
  z-index:@zindex-transfer;
}


